<template></template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { common } from '@/utils'

export default {
  name: "ReportResult",
  props: {},
  data: function() {
    // 資料
    return {
      // 返回功能資訊
      backInfo: {
        // 返回上層報表狀態
        backReportStatus: false,
        // 返回上層報表要搜尋的帳號(上上層帳號)
        backAccount: '',
        // 返回上層報表要搜尋的帳號層級(上上層帳號層級)
        backLevel: '',
        // 返回上層報表要搜尋的帳號層級線
        searchTree: {}
      },
      // 秒數倒數更新
      reciprocalItems: {
        selected: '',
        options: [
          { text: '不更新', value: '' },
          { text: '每10秒更新', value: 10 },
          { text: '每30秒更新', value: 30 }
        ],
        // 倒數 timer
        updateListTimer: null,
        // 當前秒數
        currentSpeed: '',
        // 確定更新秒數
        confirmSpeed: ''
      },
      // // 館別 (pe體彩, lt彩球, ry皇家, bg賓果) @看看能不能寫繼承@
      // gameHallItems: {},
      // 層級顯示
      stratumItems: {
        show:true,
        items: [
          { id: 1, name: '會員' },
          { id: 2, name: '代理商' },
          { id: 3, name: '總代理' },
          { id: 4, name: '股東' },
          { id: 5, name: '大股東' },
          { id: 6, name: '總監' },
          { id: 7, name: '大總監' }
        ],
        selected: [1, 2, 3, 4, 5, 6, 7]
      },
      // 搜尋條件資訊
      searchInfo: {
        // 展開狀態
        show: false,
        // 館別/項目
        itemsOfVendor: '',
        // 類型(體彩才有)
        gameType: '',
        // 玩法(體彩才有)
        playType: '',
        // 帳務日期/投注時間
        showTime: ''
      },
      // 對應層級名稱
      mapLevelAccountItems: {
        7: '大總監帳號',
        6: '總監帳號',
        5: '大股東帳號',
        4: '股東帳號',
        3: '總代理帳號',
        2: '代理帳號',
        1: '會員帳號'
      },
      // 對應館別
      // 說明: key 必須與 options value 一樣，value 必須與 options text 一樣
      mapVendorItems: {
        pe: '體彩',
        bg: 'BingoStar',
        lt: 'Super彩球',
        ry: '皇家'
      },
      // 載入資料列表
      reportList: {
        // 無資料顯示
        showNoData: '沒有匹配結果',
        // 表格欄位
        headers: [ // 各 show 值為是否顯示該列資料，由於顯示 checkbox 預設全選，所以顯示為 true
          { text: '#', value: 'id', show: true, align: 'left', sortable: true },
          { text: '帳號', value: 'account', show: true, align: 'center', sortable: false, type: 'levelAccount' },
          { text: '注單筆數', value: 'ccount', show: true, align: 'center', sortable: true },
          { text: '投注金額', value: 'cmount', show: true, align: 'center', sortable: false },
          { text: '有效投注', value: 'bmount', show: true, align: 'center', sortable: true },
          {
            text: '會員',
            stratumId: 1, // 顯示 checkbox 對應 id
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '返水', value: 'g0_comm', show: true, align: 'center', sortable: false },
              { text: '結果', value: 'g0_mount', show: true, align: 'center', sortable: true }
            ]
          },
          { // index[6]
            text: '代理商',
            stratumId: 2,
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '上級返水', text2: '未拆帳', value: 'g1_comm', show: true, align: 'center', sortable: false, open: false },
              { text: '結果', value: 'g1_mount', show: true, align: 'center', sortable: true }
            ]
          },
          { // index[7]
            text: '總代理',
            stratumId: 3,
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '上級返水', text2: '未拆帳', value: 'g2_comm', show: true, align: 'center', sortable: false, open: false },
              { text: '結果', value: 'g2_mount', show: true, align: 'center', sortable: true }
            ]
          },
          { // index[8]
            text: '股東',
            stratumId: 4,
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '上級返水', text2: '未拆帳', value: 'g3_comm', show: true, align: 'center', sortable: false, open: false },
              { text: '結果', value: 'g3_mount', show: true, align: 'center', sortable: true }
            ]
          },
          { // index[9]
            text: '大股東',
            stratumId: 5,
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '上級返水', text2: '未拆帳', value: 'g4_comm', show: true, align: 'center', sortable: false, open: false },
              { text: '結果', value: 'g4_mount', show: true, align: 'center', sortable: true }
            ]
          },
          { // index[10]
            text: '總監',
            stratumId: 6,
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '上級返水', text2: '未拆帳', value: 'g5_comm', show: true, align: 'center', sortable: false, open: false },
              { text: '結果', value: 'g5_mount', show: true, align: 'center', sortable: true }
            ]
          },
          { // index[11]
            text: '大總監',
            stratumId: 7,
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '上級返水', text2: '未拆帳', value: 'g6_comm', show: true, align: 'center', sortable: false, open: false },
              { text: '結果', value: 'g6_mount', show: true, align: 'center', sortable: true }
            ]
          },
          { text: '拆帳', value: 'bc', show: true, align: 'center', sortable: true },
          { text: '備註', value: 'l_desc', show: true, align: 'center', sortable: false }
        ],
        // 資料內容
        contents: [],
        // 底部合計
        totals: {
          betCount: 0,
          betAmount: 0,
          effectiveBet: 0,
          member: {
            g0_comm: 0,
            g0_mount: 0
          },
          agent: {
            g1_comm: 0,
            g1_mount: 0,
            g1_result: 0
          },
          generalAgent: {
            g2_comm: 0,
            g2_mount: 0,
            g2_result: 0
          },
          shareholder: {
            g3_comm: 0,
            g3_mount: 0,
            g3_result: 0
          },
          majorShareholder: {
            g4_comm: 0,
            g4_mount: 0,
            g4_result: 0
          },
          director: {
            g5_comm: 0,
            g5_mount: 0,
            g5_result: 0
          },
          majorDirector: {
            g6_comm: 0,
            g6_mount: 0,
            g6_result: 0
          }
        }
      },
      // 欲搜尋館別
      searchVendorItems: [],
      // @看看能不能寫繼承@
      // searchItems: {},
      // 頁面載入狀態
      loadingStatus: false
    };
  },
  watch: {
    'stratumItems.selected' (items) {
      // 開關各層級上級退水與未拆帳資訊
      this.switchCommission(items)
    },
    'gameHallItems.selected' (value, before) {
      if (before) {
        this.searchItems.vendor = value
        // 初始列表內容
        this.reportList.contents = []
        // 刷新列表
        this.loadResult()
      }
    },
    // 每秒更新切換
    'reciprocalItems.selected' (value) {
      this.updateTableList()
    },
    'reciprocalItems.currentSpeed' (value) {
      // 倒數為0秒時刷新列表
      if (value === 0) {
        // 刷新列表
        this.loadResult()
      }
    }
    // #這會影響 vendor 設置
    // getDashboardInfoState () {
    //   this.updateDashboardInfo()
    // }
  },
  computed: {
    ...mapGetters({
      getDashboardInfoState: 'account-management/getDashboardInfoState'
    }),
    // 層級全選
    stratumSelectAll: {
      get: function () {
        return this.stratumItems.items ? this.stratumItems.selected.length === this.stratumItems.items.length : false
      },
      set: function (value) {
        var selected = []
        if (value) {
          for (var i in this.stratumItems.items) {
            selected.push(this.stratumItems.items[i].id)
          }
        }
        this.stratumItems.selected = selected
      }
    }
  },
  methods: {
    ...mapActions({
      getTodayReport: 'api/getTodayReport',
      getHistoryReport: 'api/getHistoryReport',
      getTimeReport: 'api/getTimeReport',
      getPostReport: 'api/getPostReport',
      getDownAccount: 'api/getDownAccount'
    }),
    // 返回報表首頁
    toBack () {
      this.$router.push({
        path: this.reportUrl
      })
    },
    // (只有當日、歷史、時間報表有) @未使用?!@
    mergeTableHeader (headers) {
      const nested = !!headers.some(h => h.children)
      if (nested) {
        let children = []
        const h = headers.map(h => {
          if (h.children && h.children.length > 0) {
            children.push(...h.children)
            return {
              rowspan: 1,
              colspan: h.children.length,
              text: h.text,
              show: h.show,
              align: h.align,
              open: h.open
            }
          }
          return {
            rowspan: 2,
            colspan: 1,
            text: h.text,
            show: h.show,
            value: h.value,
            align: h.align,
            open: h.open
          }
        })
        return {
          children: children,
          parents: h
        }
      }
      return {
        parents: headers
      }
    },
    // @看看能不能寫繼承@
    // loadResult (searchItems, searchStatus) {}
    /**
     * 群組項目合計 @未使用?!@
     * @params  [dataList]     需合計的資料列
     * @params  [countItem]    需合計的資料 key 值
     * return  合計總值
     **/
    countTotal (dataList, countItem) {
      let total = 0
      for (let i in dataList) {
        total += parseFloat(dataList[i][countItem])
      }
      return total
    },
    // 開關各層級上級退水與未拆帳資訊
    switchCommission (items) {
      for (let p in this.reportList.headers) {
        // headers 各物件如有 stratumId 這 key 做該列顯示與否判斷
        if (this.reportList.headers[p].hasOwnProperty('stratumId')) {
          // 主標題，例如：會員
          this.reportList.headers[p].show = items.indexOf(this.reportList.headers[p].stratumId) !== -1
          // 副標題，例如：返水及結果
          for (let c in this.reportList.headers[p].children) {
            this.reportList.headers[p].children[c].show = items.indexOf(this.reportList.headers[p].stratumId) !== -1
          }
        }
      }
    },
    // 點帳號連結取得帳務列表
    getAccountReport (item) {
      // 點擊帳號就清除搜尋帳號
      this.searchItems.search_acc = ''

      let searchItems = this.searchItems
      // 設置 click_acc 帳號
      searchItems.click_acc = item.account
      // 設置層級線
      searchItems.search_tree = item.searchTree
      // 刷新列表
      this.loadResult(searchItems)

      // 已點擊帳號，則[返回按鈕]不返回上層報表，要返回報表首頁
      this.backInfo.backReportStatus = true

      // 取得返回上層報表的搜尋帳號
      if (item.hasOwnProperty('account') && item.account &&
          item.hasOwnProperty('level') && item.level) {
        this.getBackAccount(item.account, item.level, item.searchTree)
      }
    },
    // 依彈窗總帳館別連結取得帳務列表
    getReturnItems (items) {
      // 刷新列表 (@watch 館別就會刷新列表了!，所以這邊不用在呼叫@)
      // this.loadResult(items.searchItems)
      this.gameHallItems.selected = items.searchItems.vendor

      // 已點擊館別，則[返回按鈕]不返回上層報表，要返回報表首頁
      this.backInfo.backReportStatus = true

      // 取得返回上層報表的搜尋帳號
      this.getBackAccount(items.searchItems.click_acc, items.level, items.searchTree)
    },
    // 返回上層的報表列
    backAccountReport () {
      // 若返回欲搜尋的(上上層級)跟一開始搜尋後的(層級+1)一樣，則[返回按鈕]不返回上層報表，要返回報表首頁
      // console.log(`返回上上層級:`, this.backInfo.backLevel, `搜尋後層級+1:`, (parseInt(sessionStorage.getItem('report.search.level')) + 1))
      if (this.backInfo.backLevel === (parseInt(sessionStorage.getItem('report.search.level')) + 1)) {
        this.backInfo.backReportStatus = false
      }

      // 點擊帳號就清除搜尋帳號
      this.searchItems.search_acc = ''

      let searchItems = this.searchItems
      // 一開始搜尋有搜尋帳號，返回到(搜尋帳號的層級)不送 click_acc 參數和 search_tree 參數，則送 search_acc 參數
      if (((parseInt(this.backInfo.backLevel) - 1) === parseInt(sessionStorage.getItem('report.search.level'))) &&
          sessionStorage.getItem('report.search.account')) {
        // 設置 search_acc 帳號
        searchItems.search_acc = sessionStorage.getItem('report.search.account')

        let obj = {}
        for (let key in searchItems) {
          // 移除 click_acc 帳號和 search_tree 層級樹請求參數
          if (key !== 'click_acc' && key !== 'search_tree') {
            obj[key] = searchItems[key]
          }
        }
        searchItems = obj
      // 一開始搜尋無搜尋帳號，則需一直送 click_acc 參數和 search_tree 參數返回上層列表
      } else {
        // 設置 click_acc 帳號
        searchItems.click_acc = this.backInfo.backAccount
        // 設置層級線
        searchItems.search_tree = this.getNewSearchTree(this.backInfo.backLevel, this.backInfo.searchTree)
      }
      // 刷新列表
      this.loadResult(searchItems)

      // 取得返回上層報表的搜尋帳號
      if (this.backInfo.backAccount && this.backInfo.searchTree) this.getBackAccount(this.backInfo.backAccount, this.backInfo.backLevel, this.backInfo.searchTree)
    },
    // 取得返回上層報表的搜尋帳號
    getBackAccount (account, level, searchTree) {
      // 設置層級線
      this.backInfo.searchTree = searchTree

      switch (level) {
        case 6:
          // console.log(`返回上一層列表層級 lv: ${level}, 報表 (上上層帳號):`, searchTree.up_no6)
          this.backInfo.backAccount = searchTree.up_no6
          this.backInfo.backLevel = 7
          break
        case 5:
          // console.log(`返回上一層列表層級 lv: ${level}, 報表 (上上層帳號):`, searchTree.up_no5)
          this.backInfo.backAccount = searchTree.up_no5
          this.backInfo.backLevel = 6
          break
        case 4:
          // console.log(`返回上一層列表層級 lv: ${level}, 報表 (上上層帳號):`, searchTree.up_no4)
          this.backInfo.backAccount = searchTree.up_no4
          this.backInfo.backLevel = 5
          break
        case 3:
          // console.log(`返回上一層列表層級 lv: ${level}, 報表 (上上層帳號):`, searchTree.up_no3)
          this.backInfo.backAccount = searchTree.up_no3
          this.backInfo.backLevel = 4
          break
        case 2:
          // console.log(`返回上一層列表層級 lv: ${level}, 報表 (上上層帳號):`, searchTree.up_no2)
          this.backInfo.backAccount = searchTree.up_no2
          this.backInfo.backLevel = 3
          break
        case 1:
          // console.log(`返回上一層列表層級 lv: ${level}, 報表 (上上層帳號):`, searchTree.up_no1)
          this.backInfo.backAccount = searchTree.up_no1
          this.backInfo.backLevel = 2
          break
      }
    },
    /**
     * 秒數倒數
     * @params  [speed]  倒數秒數
     **/
    startCountdown (speed) {
      this.reciprocalItems.currentSpeed = speed
      this.reciprocalItems.updateListTimer = setInterval(() => {
        this.reciprocalItems.currentSpeed--
        // 秒數為負數，則重新倒數
        if (this.reciprocalItems.currentSpeed < 0) {
          // 清除 timer
          clearInterval(this.reciprocalItems.updateListTimer)
          // 重新倒數秒數
          this.startCountdown(this.reciprocalItems.confirmSpeed)
        }
      }, 1000)
    },
    // 啟動秒數倒數刷新表格資料
    updateTableList () {
      // 刷新列表
      this.loadResult()

      // 確定更新秒數
      this.reciprocalItems.confirmSpeed = this.reciprocalItems.selected

      // 清除 timer
      clearInterval(this.reciprocalItems.updateListTimer)

      // 秒數為負數，則不更新倒數功能
      if (this.reciprocalItems.selected < 0) {
        // 當前秒數設置空值
        this.reciprocalItems.currentSpeed = ''
        return
      }
      // 倒數秒數
      this.startCountdown(this.reciprocalItems.confirmSpeed)
    },
    // 更新層級勾選項目
    updateStratum (loginLevel) {
      let newStratumItems = []
      let newStratumSelected = []
      for (let i in this.stratumItems.items) {
        if (this.stratumItems.items[i].id <= parseInt(loginLevel)) newStratumItems.push(this.stratumItems.items[i])
      }
      this.stratumItems.items = newStratumItems
      for (let i = 1; i <= parseInt(loginLevel); i++) {
        newStratumSelected.push(i)
      }
      this.stratumItems.selected = newStratumSelected
    },
    // 開關館別選項
    switchVendor () {
      // 有選擇 體彩、BingoStar、Super彩球、皇家
      if (this.searchVendorItems.indexOf('pe') !== -1 &&
          this.searchVendorItems.indexOf('bg') !== -1 &&
          this.searchVendorItems.indexOf('lt') !== -1 &&
          this.searchVendorItems.indexOf('ry') !== -1) {
        for (let i in this.gameHallItems.options) {
          if (this.gameHallItems.options[i].type === 'all') { // 「全部」選項
            // 若無返回帶館別參數，則用預設選項
            if (!this.$route.query.searchVendorItems) {
              // this.gameHallItems.selected = 'pe,bg,lt,ry'
              // 有搜尋彩球項目，預設要「全部(不包含Super彩球)」選項
              this.gameHallItems.selected = 'pe,bg,ry'
            }
            this.gameHallItems.options[i].value = 'pe,bg,lt,ry'
            this.gameHallItems.options[i].show = true
          }
          if (this.gameHallItems.options[i].type === 'allNoLottery') { // 「全部(不包含Super彩球)」選項
            this.gameHallItems.options[i].value = 'pe,bg,ry'
            this.gameHallItems.options[i].show = true
          }
        }
      // 有選擇 BingoStar、Super彩球、皇家
      } else if (this.searchVendorItems.indexOf('bg') !== -1 &&
          this.searchVendorItems.indexOf('lt') !== -1 &&
          this.searchVendorItems.indexOf('ry') !== -1) {
        for (let i in this.gameHallItems.options) {
          if (this.gameHallItems.options[i].type === 'all') { // 「全部」選項
            // 若無返回帶館別參數，則用預設選項
            if (!this.$route.query.searchVendorItems) {
              // this.gameHallItems.selected = 'bg,lt,ry'
              // 有搜尋彩球項目，預設要「全部(不包含Super彩球)」選項
              this.gameHallItems.selected = 'bg,ry'
            }
            this.gameHallItems.options[i].value = 'bg,lt,ry'
            this.gameHallItems.options[i].show = true
          }
          if (this.gameHallItems.options[i].type === 'allNoLottery') { // 「全部(不包含Super彩球)」選項
            this.gameHallItems.options[i].value = 'bg,ry'
            this.gameHallItems.options[i].show = true
          }

          if (this.gameHallItems.options[i].type === 'sport') {
            this.gameHallItems.options[i].show = false
          }
        }
      // 有選擇 體彩、Super彩球、皇家
      } else if (this.searchVendorItems.indexOf('pe') !== -1 &&
          this.searchVendorItems.indexOf('lt') !== -1 &&
          this.searchVendorItems.indexOf('ry') !== -1) {
        for (let i in this.gameHallItems.options) {
          if (this.gameHallItems.options[i].type === 'all') { // 「全部」選項
            // 若無返回帶館別參數，則用預設選項
            if (!this.$route.query.searchVendorItems) {
              // this.gameHallItems.selected = 'pe,lt,ry'
              // 有搜尋彩球項目，預設要「全部(不包含Super彩球)」選項
              this.gameHallItems.selected = 'pe,ry'
            }
            this.gameHallItems.options[i].value = 'pe,lt,ry'
            this.gameHallItems.options[i].show = true
          }
          if (this.gameHallItems.options[i].type === 'allNoLottery') { // 「全部(不包含Super彩球)」選項
            this.gameHallItems.options[i].value = 'pe,ry'
            this.gameHallItems.options[i].show = true
          }

          if (this.gameHallItems.options[i].type === 'bingo') {
            this.gameHallItems.options[i].show = false
          }
        }
      // 有選擇 體彩、BingoStar、皇家
      } else if (this.searchVendorItems.indexOf('pe') !== -1 &&
          this.searchVendorItems.indexOf('bg') !== -1 &&
